import React from 'react'
import{styled,Typography} from '@mui/material'
import {Box,Container} from '@mui/system'
import deliveryImg from '../../assets/DeliveryImg.png'
import CustomButton from '../../components/CustomButton/CustomButton'

const Delivery =()=>{
    const CustomContainer = styled(Container)(({theme})=>({
        backgroundColor:'#FED801',
        height:'416px',
        borderRadius:'15px',
        display:'flex',
        justifyContent:'space-around',
        alignItems:'center',
        [theme.breakpoints.down('md')]:{
            height:'auto',
            flexDirection:'column',
            alignItems:'center',
            padding:theme.spacing(3,3,0,3),
            width:'90%',

        }
    }))
    const CustombBox = styled(Box)(({theme})=>({
       padding:theme.spacing(10,0,10,0),
       margin:theme.spacing(0,2,0,2),
        [theme.breakpoints.down('md')]:{
           padding:'0',

        }
    }))

    return(
        <CustombBox>
            <CustomContainer>
                <Box>
                    <Typography
                    sx={{fontSize:'35px',color:'white',fontWeight:'700',fontFamily:'monospace'}}
                    >
                        Supper Fast Home Delivery!

                    </Typography>
                    <Typography
                    sx={{fontSize:'16px',color:'#000',fontWeight:'800',my:3,fontFamily:'monospace'}}
                    >
                        Door to Door Delivery!

                    </Typography>
                    <CustomButton
                    backgroundColor='#fff'
                    color='#17275F'
                    buttonText='Order Now'
                    />
                </Box>
                <img src={deliveryImg} alt="delivery" style={{maxWidth:'100%'}} />
            </CustomContainer>
        </CustombBox>
    )
}

export default Delivery